h1{
  text-align: center;
  margin-top: 10px;
  font-weight: bolder;
  font-family: cursive;
}
.card {
  text-align: center;
  /* max-width: 18rem; */
  width: 25rem;
  box-shadow: 10px 10px 8px #888888;
  position: relative;
  left: 38%;
  margin-top: 5%;
}
#task{
  margin-top: 0.5rem;
}
#sub{
  transition: all .2s;
}
#sub:hover{
  background-color: rgb(134, 174, 211);
  border-radius: 10%;
  font-weight: bold;
  transform: scale(1.05);
}
.jai{
  background-color: #92f7f7;
  margin: 15px;
  box-shadow: 8px 5px 9px -2px #888888;
  align-items: center;
}
.font {
  font-size: .8rem;
  padding: 1px;
  margin: 4px 10px;
}

@media only screen and (max-width: 1170px) and (min-width:775px) {
  .card{
    position: relative;
  left: 25%;
  margin-top: 10%;
  }
}

@media only screen and (max-width: 775px){
  .card{
    width: 17rem;
    position: relative;
  left: 19%;
  margin-top: 10%;
  }
}